import React, { Component } from 'react';
import { History } from 'history';
import { Form, Icon, Input, Button, Checkbox } from 'antd';
import './index.less';

interface IProps {
  form: any;
  history: History;
}
class Login extends Component<IProps> {
  public handleSubmit = () => {
    const { history } = this.props;
    history.push('/');
    console.log(this.props);
  };
  public render() {
    const { getFieldDecorator } = this.props.form;
    return (
      <div className="login" style={{ backgroundImage: `url(${require('@/assets/loginbg.svg')})` }}>
        <div className="login-logo" />
        <div className="login-main">
          <div className="logo">
            <div className="logo-text">违法鸣笛抓拍管理平台</div>
            <div className="logo-en">ITS MANAGEMENT SYSTEM</div>
          </div>
          <Form onSubmit={this.handleSubmit} className="login-form">
            <Form.Item>
              {getFieldDecorator('账号', {
                rules: [{ required: true, message: '请输入账号!' }]
              })(<Input size="large" prefix={<Icon type="user" style={{ color: 'rgba(0,0,0,.25)' }} />} placeholder="请输入账号" />)}
            </Form.Item>
            <Form.Item>
              {getFieldDecorator('密码', {
                rules: [{ required: true, message: '请输入密码!' }]
              })(<Input size="large" prefix={<Icon type="lock" style={{ color: 'rgba(0,0,0,.25)' }} />} type="password" placeholder="请输入密码" />)}
            </Form.Item>
            <Form.Item>
              {getFieldDecorator('remember', {
                valuePropName: 'checked',
                initialValue: true
              })(<Checkbox>记住密码</Checkbox>)}
              <a className="login-form-forgot" href="forget">
                忘记密码
              </a>
            </Form.Item>
            <Form.Item>
              <Button size="large" type="primary" htmlType="submit" block={true}>
                登录
              </Button>
              <Button size="large" block={true}>
                重置
              </Button>
            </Form.Item>
          </Form>
        </div>
      </div>
    );
  }
}

export default Form.create({ name: 'login' })(Login);
